import { Component, h, Host, Method, Prop } from '@stencil/core';
import {
    BarController,
    BarElement,
    CategoryScale,
    Chart,
    LinearScale,
    LineController,
    LineElement,
    TimeScale,
    Legend,
    Tooltip,
    PointElement,
    Filler,
    ChartConfiguration,
    PointStyle,
    Colors,
    ChartDataset,
    Point,
    BubbleDataPoint,
    Color,
} from 'chart.js';
import 'chartjs-adapter-moment';
import type { TimeWithUnit } from '../../types';
import { TimeDateUtil } from '../../utils/time-date.util';
import { SelectedTimeRangeExtended } from '../../components';
import { Height } from '../at-chart-donut/at-chart-donut';
import { ChartColorPalette } from '../../types/chart-color';
import { getChartColors } from '../../utils/chart-color';

/*
 * NOTES:
 *
 * The exported class contains a function from chart.js called useOffsetPos.
 * This isn't allowed by react (because it starts with the word 'use'), so the
 * build script runs
 *
 *  sed -i 's/useOffsetPos/offsetPos/g' dist/components/at-chart-bar-line.js
 *
 * to replace all occurences of useOffsetPos with offsetPos.
 */
export interface PointStyles {
    pointBackgroundColor: Color;
    pointBorderColor: Color;
    pointBorderWidth: number;
    pointHitRadius: number;
    pointHoverBackgroundColor: Color;
    pointHoverBorderColor: Color;
    pointHoverBorderWidth: number;
    pointHoverRadius: number;
    pointRadius: number;
    pointRotaion: number;
    pointStyle: PointStyle;
}

export interface Threshold {
    label: string;
    data: (number | Point | [number, number] | BubbleDataPoint)[];
    color?: string;
    dashLine?: boolean;
    stepped?: boolean;
}

const heightVariants = {
    xs: 'h-[70px]',
    sm: 'h-[160px]',
    md: 'h-[235px]',
    lg: 'h-[330px]',
    xl: 'h-[440px]',
    auto: 'h-full',
};

/**
 * @category Data Visualization
 * @description A flexible chart component for displaying bar and line charts with extensive customization options. Supports multiple data series, thresholds, time-based data, and various color palettes.
 */
/**
 * ### ```interface PointStyles```
 * ```
 *  pointBackgroundColor: Color;
 *  pointBorderColor: Color;
 *  pointBorderWidth: number;
 *  pointHitRadius: number;
 *  pointHoverBackgroundColor: Color;
 *  pointHoverBorderColor: Color;
 *  pointHoverBorderWidth: number;
 *  pointHoverRadius: number;
 *  pointRadius: number;
 *  pointRotaion: number;
 *  pointStyle: PointStyle;
 * ```
 * ### ```interface Threshold```
 * ```
 *  label: string;
 *  data: (number | Point | [number, number] | BubbleDataPoint)[];
 *  color?: string;
 *  dashLine?: boolean;
 *  stepped?: boolean;
 * ```
 */
@Component({
    tag: 'at-chart-bar-line',
    shadow: false,
})
export class AtChartBarLine {
    /**
     * Type of the chart
     */
    @Prop()
    type: 'bar' | 'line';

    /**
     * Data to go in the chart. ChartDataset properties found
     * [here for line](https://www.chartjs.org/docs/latest/charts/line.html),
     * and [here for bar](https://www.chartjs.org/docs/latest/charts/bar.html)
     */
    @Prop({ mutable: true })
    data: {
        labels: string[];
        datasets: ChartDataset[];
    };

    /**
     * X axis formatting to be applied to the chart.
     * If you would like the default formatting provided by chart.js, set this to an empty object.
     */
    @Prop()
    x_axis_format: object;

    /**
     * Y axis formatting to be applied to the chart.
     */
    @Prop()
    y_axis_format?: object;

    /**
     * Additional options to go into the 'options' seciont of the chart configuration
     */
    @Prop()
    options?: object;

    /**
     * Options for the tooltips on the chart
     */
    @Prop()
    tooltip_options?: object;

    /**
     * Options for the legend
     */
    @Prop()
    legend_format?: object;

    /**
     * Thresholds to be displayed in the chart
     */
    @Prop()
    thresholds?: Threshold[];

    /**
     * Styles for the points in the chart
     */
    @Prop()
    point_styles?: PointStyles;

    /**
     * The time range that the chart is displayed in.
     */
    @Prop()
    time_range?: SelectedTimeRangeExtended;

    /**
     * Height of the chart.
     */
    @Prop()
    height?: Height = 'auto';
    canvasEl: HTMLCanvasElement;
    config: ChartConfiguration;

    /**
     * Colour palette to use for the chart. Preset options are provided ChartColourPalette:
     * 'categorical' : For charts with data that have distinct labels and no natural order
     * 'sequential' : For charts with data that is numeric or is naturally ordered.
     * 'alert' : For charts that relate to health state. Note that data requires a specific order.
     * 'custom' : Use colors defined in data. If none are provided, the ChartJS default will be used.
     */
    @Prop()
    color_palette: ChartColorPalette = ChartColorPalette.CATEGORICAL;

    /**
     * Getter method for the chart's configuration object
     * @returns Configuration of the chart
     */
    @Method()
    async getConfig(): Promise<object> {
        return this.config;
    }

    initChart() {
        Chart.register(
            LinearScale,
            BarController,
            CategoryScale,
            BarElement,
            TimeScale,
            LineController,
            LineElement,
            PointElement,
            Colors,
            Legend,
            Tooltip,
            Filler,
        );

        const colors = getChartColors(this.color_palette);

        if (colors) {
            this.applyPresetPalette(colors);
        }

        if (this.point_styles) {
            this.pointStylesSetup();
        }

        this.config = {
            type: this.type,
            data: {
                labels: this.data.labels,
                datasets: [...this.data.datasets, ...this.formatThresholds()],
            },
            options: {
                devicePixelRatio: 2,
                maintainAspectRatio: false,
                scales: {
                    y: this.y_axis_format || {
                        beginAtZero: true,
                        type: 'linear',
                        min: 0,
                    },
                    x: this.x_axis_format || {
                        type: 'time',
                        time: {
                            displayFormats: {
                                day: 'ddd',
                                minute: 'MMM D H:mm',
                                hour: 'MMM D H:mm',
                            },
                        },
                        ticks: {
                            font: {
                                size: 11,
                            },
                            autoSkip: true,
                            align: 'center',
                            maxRotation: 0,
                            minRotation: 0,
                        },
                    },
                },
                ...this.options,
                plugins: {
                    tooltip: this.tooltip_options || {
                        mode: 'index',
                        intersect: false,
                        position: 'nearest',
                        animation: {
                            duration: 150,
                        },
                    },
                    legend: this.legend_format || {
                        labels: {
                            boxWidth: 8,
                            boxHeight: 8,
                            fontSize: 10,
                            borderRadius: 2,
                        },
                        onHover: (event): void => {
                            if (event.native) {
                                (
                                    event.native.target as HTMLElement
                                ).style.cursor = 'pointer';
                            }
                        },
                        display: true,
                    },
                },
                clip: false,
                elements: {
                    line: {
                        tension: 0,
                        borderWidth: 2,
                    },
                },
            },
        };

        if (this.time_range) {
            const dates = this.getMinMaxDateStrings(this.time_range);
            this.config.options.scales.x['min'] = dates.min;
            this.config.options.scales.x['max'] = dates.max;
        }

        new Chart(this.canvasEl, this.config);
        this.canvasEl.style.width = '';
        this.canvasEl.style.height = '';
    }

    componentDidUpdate() {
        if (this.data && this.data.datasets.length) {
            this.initChart();
        }
    }

    /**
     * componentDidLoad will be run, but if the props haven't been passed to it yet,
     * it will throw an error. So we catch this, and wait for the props to be passed
     * to the component where it will run componentDidUpdtae.
     */
    componentDidLoad() {
        if (this.data && this.data.datasets.length) {
            this.initChart();
        }
    }

    pointStylesSetup(): void {
        this.data.datasets = this.data.datasets.map((dataset) => {
            return {
                ...dataset,
                ...(this.point_styles as object),
            };
        });
    }

    applyPresetPalette(colors: string[]): void {
        if (this.color_palette === ChartColorPalette.CUSTOM) {
            return;
        }

        this.data.datasets = this.data.datasets.map((dataset, index) => {
            const color = colors[index % colors.length];
            return {
                ...dataset,
                backgroundColor: color,
                borderColor: color,
            };
        });
    }

    getMinMaxDateStrings(timeRange: SelectedTimeRangeExtended): {
        min: string;
        max: string;
    } {
        const timeWithUnit = timeRange.selected as TimeWithUnit;
        const custom = timeRange.custom
            ? TimeDateUtil.getAbsoluteDateRange(timeRange.custom)
            : null;
        const { startDate, endDate } = TimeDateUtil.getDateRange(
            custom,
            timeWithUnit,
            null,
        );
        return { min: startDate.toString(), max: endDate.toString() };
    }

    formatThresholds = () => {
        if (!this.thresholds) {
            return [];
        }
        const datasets: ChartDataset[] = [];
        this.thresholds.forEach((threshold) => {
            datasets.push({
                type: 'line',
                label: threshold.label,
                borderColor: threshold.color,
                borderCapStyle: 'butt',
                backgroundColor: 'white',
                fill: false,
                data: threshold.data,
                pointRadius: 0,
                borderDash: threshold.dashLine ? [6, 3] : undefined,
                tension: 0,
                stepped: threshold.stepped,
                order: 1,
            });
        });
        return datasets;
    };

    render() {
        return (
            <Host style={{ height: '100%', width: '100%' }}>
                <section class={`min-w-100 ${heightVariants[this.height]}`}>
                    <canvas ref={(el) => (this.canvasEl = el)} />
                </section>
            </Host>
        );
    }
}
